import Vue from 'vue'
import App from './App.vue'
import './scss/app.scss';
import Antd from 'ant-design-vue';

import Default_Layout from './layouts/Default.vue'
import Dashboard_Layout from './layouts/Dashboard.vue'
import DashboardRTL_Layout from './layouts/DashboardRTL.vue'

Vue.config.productionTip = false
import VueRouter from 'vue-router'
// Adding template layouts to the vue components.
Vue.component("layout-default", Default_Layout);
Vue.component("layout-dashboard", Dashboard_Layout);
Vue.component("layout-dashboard-rtl", DashboardRTL_Layout);
import router from './router'

Vue.use(Antd);
Vue.use(VueRouter)
new Vue({
    router,
    render: h => h(App),
}).$mount('#app')
